body, td, th {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.error-msg {
    color: red;
    text-align: center;
    font-size: 18px;
  }

  .am-tab-bar {
    position: fixed;
    bottom: 0;
    width: 100%;
    height: inherit; /*包裹内容， 内容多高我就多高*/
  }

  .sticky-header{
    z-index: 10;
    position: fixed;
    top: 0;
    width: 100%;
  }

  .chat-page .chat-me .am-list-extra {
    flex-basis: auto;
  }
  
  .chat-page .chat-me .am-list-content {
    padding-right: 15px;
    text-align: right;
  }

  .am-navbar {
    font-weight: bold;
    background-color: lightseagreen;
  }

  .am-button-primary {
    font-weight: bold;
    background-color: lightseagreen;
  }
